import meSideMenu from "@/components/meSideMenu";
import { MY_DEALS } from '@/api/api';
export default {
  name: "deals",
  components: {
    meSideMenu
  },

  data() {
    return {
      userInfo: {},
      basicUserInfo: {},
      dealsListData: [],
      dealPage: 1,
      dealLimit: 10,
      dealTotalNum: 0
    };
  },

  mounted() {
    this.getMyDeals(this.dealPage, this.dealLimit);
  },

  methods: {
    postDeal() {
      this.$router.push({
        path: '/deals/offer',
        query: {}
      });
    },

    turnDealDetail(id) {
      this.$router.push({
        path: '/deals/detail',
        query: {
          id: id
        }
      });
    },

    dealPageSizeChange(e) {
      console.log(e);
    },

    dealPageChange(e) {
      this.jobPage = e;
      this.getMyDeals(e, this.dealLimit);
      console.log(e);
    },

    getMyDeals(page, limit) {
      let params = {
        token: localStorage.getItem('token'),
        page: page,
        limit: limit
      };
      MY_DEALS(params).then(res => {
        console.log(res);

        if (res.code == 200) {
          this.dealsListData = res.message.data; // console.log(res.message.data)

          this.dealTotalNum = res.message.total;
        } else {
          console.log(res.msg);
        }
      }).catch(err => {
        console.log(err);

        if (err.msg) {
          this.$message.error(err.msg);
        }

        if (err.message) {
          this.$message.error(err.message);
        }
      });
    }

  }
};